@import "../../scss/base";

@mixin skillsHeading {
  font-size: 1.05rem;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}

.Tech {
  margin-top: 5rem;
  padding-bottom: 5rem;

  &__heading {
    @include heading;
    padding: 0;
    margin-top: 2rem;
    line-height: 1;
  }

  &__text--secondaryColor {
    @include secondaryColor;
  }

  &__servicesHeading {
    text-align: center;
    letter-spacing: 0.3rem;
    margin: 3rem;
  }

  &__servicesGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }

  &__service {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 1rem 1rem;
  }

  &__serviceIcon {
    color: $secondary-color;
    font-size: 3rem;
  }

  &__serviceCaption {
    padding: 3px 0;
    margin: 0;
  }

  &__serviceDetail {
    text-align: center;
  }

  &__skillsGrid {
    display: grid;
    grid-gap: 1.5rem;
    margin-top: 2rem;
    grid-template-columns: 1fr;
    grid-template-areas:
      "fe"
      "be"
      "do";

    @include mediaLarge {
      grid-template-columns: 1fr 1fr;
      grid-template-areas:
        "fe be"
        "do do";
    }
  }

  &__skillsHeading {
    @include skillsHeading;
    background: lighten($primary-color, 5);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    padding: 0 1rem;
  }

  &__skillsBorder {
    border: none;
    background: lighten($primary-color, 5);
    border-bottom: 0.3rem solid $secondary-color;
    border-top: 0.3rem solid $secondary-color;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);

    &--frontEnd {
      grid-area: fe;
    }

    &--backEnd {
      grid-area: be;
    }

    &--devOps {
      grid-area: do;
    }
  }

  &__skills {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
  }
}
